@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
:root {
  --accent-color: #f87719;
  --dark-color: #111;
  --light-color: #fff;
  --light-theme-dark-text-color: #282828;
  --light-theme-medium-text-color: 5959#59;
  --dark-theme-dark-text-color: #b7b7b7;
  --dark-theme-light-text-color: #f8f8f8;
  --linear-gradient-first: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-gradient-second: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --action-transition: cubic-bezier(.4, 0, .2, 1);
  --regular: 400;
  --medium: 500;
  --bold: 700;
}

::-webkit-scrollbar {
  width: 10px;
  height: 0;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 5px gray;
}

::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-color);
}

body, button, link, textarea, input {
  font-family: Roboto, sans-serif;
}

body {
  background-color: var(--dark-color);
  cursor: url("cursor.bf1d0651.svg"), default;
}

button, a, input, select {
  cursor: url("cursor-pointer.1434d615.svg"), pointer;
}

.btn {
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: normal;
  display: flex;
}

.link {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.3);
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.main-accent-btn {
  background: var(--linear-gradient-first);
  border: none;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.56;
}

.main-light-btn {
  color: var(--accent-color);
  background-color: #fff;
  border: none;
  margin: 1px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.56;
  display: inline-block;
  position: relative;
}

.main-light-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.main-accent-sml-btn {
  background: var(--linear-gradient-first);
  border: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.14;
}

.main-light-sml-btn {
  color: var(--accent-color);
  background-color: #fff;
  border: none;
  margin: 1px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.14;
  display: inline-block;
  position: relative;
}

.main-light-sml-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.rm-dark-bcg-btn {
