  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.rm-light-bcg-btn {
  color: var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-size: 12px;
  line-height: 1.33;
}

.rm-light-bcg-grd-btn {
  background-color: var(--light-color);
  color: var(--dark-color);
  border: none;
  margin: 1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: inline-block;
  position: relative;
}

.rm-light-bcg-grd-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
