}

.film-card-info {
  max-width: 100%;
  z-index: 2;
  font-size: 12px;
  line-height: 1.66667;
  font-weight: var(--medium);
  position: absolute;
  bottom: 12px;
  left: 12px;
  right: 12px;
}

.film-name {
  text-transform: uppercase;
  color: var(--light-color);
  margin: 0;
}

.film-wrap {
  justify-content: space-between;
  align-items: end;
  margin: 0;
  display: flex;
}

.film-feature {
