  height: 12.25px;
}

@media screen and (min-width: 768px) {
  .film-card {
    width: 224px;
    height: 325px;
  }

  .film-card-info {
    line-height: 1.5;
  }

  .rating-item {
    width: 10px;
    height: 10px;
  }

  .rating-icon {
