.header {
  width: 100%;
  background-color: var(--dark-color);
  height: 48px;
  padding-top: 8px;
  padding-bottom: 8px;
  top: 40px;
}

body.light .header {
  background-color: var(--light-color);
}

.current {
  color: var(--accent-color);
}

.header-content-wrap {
  max-width: 280px;
  max-height: 32px;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-pic {
  width: 32px;
  height: 32px;
}

.logo-text, .header-nav {
  display: none;
}

.mobile-menu-link {
  font-size: 12px;
  font-weight: var(--medium);
  color: var(--dark-theme-dark-text-color);
  margin-left: auto;
  margin-right: auto;
  line-height: 14.06px;
}

body.light .mobile-menu-link {
  color: var(--light-theme-medium-text-color);
}

label {
  pointer-events: none;
}

.input {
  display: none;
}

.toggle-wrapper {
  width: 44px;
  height: 18px;
  background: var(--linear-gradient-first);
  cursor: url("cursor-pointer.1434d615.svg"), pointer;
  pointer-events: all;
  border: none;
  border-radius: 194px;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.selector {
  transition: all .25s ease-out;
  position: absolute;
  top: 3.5px;
  left: 3px;
  right: 3px;
  transform: translateY(-15%);
}

body.light .toggle-wrapper:not(:checked) .handle-icon {
  transform: rotate(180deg);
}

.handle-icon {
  width: 13.2px;
  height: 13.5px;
  fill: var(--accent-color);
}

.moon-icon {
  width: 9.53px;
  height: 9.75px;
  margin-top: 4px;
  margin-left: 3px;
}

.sun-icon {
  width: 10px;
  height: 10px;
  margin-top: 4px;
  margin-right: 4.5px;
}

:checked + .toggle-wrapper > .selector {
  left: calc(90% - 11.5px);
}

.mobile-backdrop {
  z-index: 3;
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: visibility .5s cubic-bezier(.4, 0, .2, 1), opacity .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.mobile-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.3);
}

body.light .mob-menu {
  background-color: var(--dark-theme-light-text-color);
}

body.light .mob-nav-link {
  color: var(--dark-color);
}

body.light .mob-nav-link:focus {
  color: var(--accent-color);
}

.mob-menu {
  width: 204px;
  height: 100%;
  background: #171717;
  border-radius: 0 8px 8px 0;
  padding-top: 12px;
  padding-left: 20px;
  transition: left .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  left: 0;
}

.menu-hidden {
  left: -100%;
}

.mob-logo {
  margin-bottom: 44px;
}

.mob-nav-list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.mob-nav-link {
  text-transform: uppercase;
  color: var(--light-color);
  font-size: 14px;
  font-weight: var(--medium);
}

.mob-nav-link:focus {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .header {
    min-height: 60px;
    padding-top: 12px;
    padding-bottom: 12px;
  }

  .mobile-menu-link, .mobile-backdrop {
    display: none;
  }

  .header-content-wrap {
    max-width: 704px;
    max-height: 48px;
    justify-content: space-between;
  }

  .header-logo-wrap {
    justify-content: flex-start;
    display: flex;
  }

  .logo-pic {
    width: 48px;
    height: 48px;
  }

  .logo-text {
    font-size: 24px;
    line-height: 1.16667;
    font-weight: var(--regular);
    color: var(--dark-theme-light-text-color);
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 8px;
    display: block;
  }

  body.light .logo-text {
    color: var(--light-theme-dark-text-color);
  }

  .header-nav {
    margin-left: 59px;
    margin-right: auto;
    display: flex;
  }

  .header-nav-list {
    gap: 32px;
    display: flex;
  }

  .header-nav-item {
    color: var(--dark-theme-dark-text-color);
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 14px;
  }

  body.light .header-nav-item {
    color: var(--light-theme-medium-text-color);
  }

  .header-nav-link:is(:hover, :focus) {
    color: var(--accent-color);
  }

  .toggle-wrapper {
    width: 60px;
    height: 24px;
    margin-left: auto;
  }

  .selector {
    top: 3px;
    left: 3.5px;
    right: 3.5px;
    transform: translateY(-2.5%);
  }

  :checked + .toggle-wrapper > .selector {
    left: calc(90% - 16px);
  }

  .handle-icon {
    width: 18px;
    height: 18px;
  }

  .moon-icon {
    width: 13px;
    height: 13px;
    margin-top: 5px;
    margin-left: 4px;
  }

  .sun-icon {
    width: 14px;
    height: 14px;
    margin-top: 5px;
    margin-right: 5px;
  }

  .mob-menu-btn, .modal-backdrop.backdrop-mob-menu {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .header-content-wrap {
    max-width: 1216px;
  }

  .header-nav {
    margin-left: auto;
  }
}

